<template lang="html">
  <div class="ratings" ref="ratings">
    <div class="ratings-content">
      <div class="overview">
        <div class="overview-left">
          <h1 class="score">{{seller.score}}</h1>
          <p class="title">综合评分</p>
          <p class="rank">高于周边商家{{seller.rankRate}}</p>
        </div>
        <div class="overview-right">
          <div class="score-wrap">
            <span class="title">服务态度</span>
            <star :size="36" :score="seller.serviceScore"></star>
            <span class="score">{{seller.serviceScore}}</span>
          </div>
          <div class="score-wrap">
            <span class="title">商品评分</span>
            <star :size="36" :score="seller.foodScore"></star>
            <span class="score">{{seller.foodScore}}</span>
          </div>
          <div class="delivery-wrap">
            <span class="title">送到时间</span>
            <span class="delivery">{{seller.deliveryTime}}分钟</span>
          </div>
        </div>
      </div>
      <split></split>
      <ratingselect @select="selectRating" @toggle="toggleContent" :ratings="ratings" :selectType="selectType" :onlyContent="onlyContent"></ratingselect>
      <div class="ratings-wrap">
        <ul>
          <li v-for="rating in ratings" class="rating-item" v-show="needShow(rating.rateType, rating.text)">
            <div class="avatar"><img :src="rating.avatar" width="28" height="28"></div>
            <div class="content">
              <h1 class="username">{{rating.username}}</h1>
              <div class="star-wrap">
                <star :size="24" :score="rating.score"></star>
                <span class="delivery" v-show="rating.delivery">{{rating.deliveryTime}}</span>
              </div>
              <p class="text">{{rating.text}}</p>
              <div class="recommend" v-show="rating.recommend.length">
                <span class="icon-thumb_up"></span>
                <span class="item" v-for="item in rating.recommend">{{item}}</span>
              </div>
              <div class="time">
                {{rating.rateTime | formatDate}}
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import star from 'components/star/star'
import split from 'components/split/split'
import ratingselect from 'components/ratingselect/ratingselect'
import {formatDate} from '../../assets/js/date.js'
import BScroll from 'better-scroll'

const ERR_OK=0
const ALL=2

export default {
  name:'ratings',
  props:{
    seller:{
      type:Object
    }
  },
  data:function(){
    return {
      ratings:[],
      selectType:ALL,
      onlyContent:true,
    }
  },
  created:function(){
    axios.get('/api/ratings').then((response)=>{
      if (response.data.errno === ERR_OK){
        this.ratings = response.data.data
        this.$nextTick(() => {
            this.scroll = new BScroll(this.$refs.ratings, {
              click: true
            })
          })
      }
    })
  },
  methods:{
    needShow:function(type,text){
      if(this.onlyContent && !text){
        return false
      }
      if(this.selectType===ALL){
        return true
      }else{
        return this.selectType===type
      }
    },
    selectRating(type) {
        this.selectType = type
        this.$nextTick(() => {
          this.scroll.refresh()
        })
      },
    toggleContent() {
      this.onlyContent = !this.onlyContent;
      this.$nextTick(() => {
        this.scroll.refresh()
      })
    }
  },
  filters:{
    formatDate:function(time){
      let date=new Date(time)
      return formatDate(date,'yyyy-MM-dd hh:mm')
    }
  },
  components:{star,split,ratingselect}
}
</script>

<style lang="stylus">
@import '../../assets/stylus/mixin.styl'

.ratings
  position:absolute
  top:174px
  bottom:0px
  width:100%
  overflow:hidden
  .overview
    display:flex
    padding:18px 0
    .overview-left
      flex-shrink:0
      flex-basis:137px
      width:137px
      padding:6px 0
      border-right:1px solid rgba(7,17,27,0.1)
      text-align:center
      @media only screen and (max-width: 320px)
        flex-shrink:0
        flex-basis:120px
        width: 120px
      .score
        font-size:24px
        line-height:28px
        color:rgb(255,153,0)
        margin-bottom:6px
      .title
        font-size:12px
        line-height:12px
        color:rgb(7,17,27)
        margin-bottom:8px
      .rank
        font-size:10px
        line-height:10px
        color:rgb(147,153,159)
    .overview-right
      flex-grow:1
      padding:6px 0 6px 24px
      @media only screen and (max-width: 320px)
        padding-left: 6px
      .score-wrap
        margin-bottom:8px
        font-size:0
        .title
          display:inline-block
          line-height:18px
          font-size:12px
          color:rgb(7,17,27)
          vertical-align:top
        .star
          display:inline-block
          margin:0 12px
          vertical-align:top
        .score
          display:inline-block
          line-height:18px
          vertical-align:top
          font-size:12px
          color:rgb(255,153,0)
      .delivery-wrap
        font-size: 0
        .title
          line-height: 18px
          font-size: 12px
          color: rgb(7, 17, 27)
        .delivery
          margin-left: 12px
          font-size: 12px
          color: rgb(147, 153, 159)
  .ratings-wrap
    padding: 0 18px
    .rating-item
      display: flex
      padding: 18px 0
      border-1px(rgba(7, 17, 27, 0.1))
      .avatar
        flex: 0 0 28px
        width: 28px
        margin-right: 12px
        img
          border-radius: 50%
      .content
        position: relative
        flex: 1
        .name
          margin-bottom: 4px
          line-height: 12px
          font-size: 10px
          color: rgb(7, 17, 27)
        .star-wrapper
          margin-bottom: 6px
          font-size: 0
          .star
            display: inline-block
            margin-right: 6px
            vertical-align: top
          .delivery
            display: inline-block
            vertical-align: top
            line-height: 12px
            font-size: 10px
            color: rgb(147, 153, 159)
        .text
          margin-bottom: 8px
          line-height: 18px
          color: rgb(7, 17, 27)
          font-size: 12px
        .recommend
          line-height: 16px
          font-size: 0
          .icon-thumb_up, .item
            display: inline-block
            margin: 0 8px 4px 0
            font-size: 9px
          .icon-thumb_up
            color: rgb(0, 160, 220)
          .item
            padding: 0 6px
            border: 1px solid rgba(7, 17, 27, 0.1)
            border-radius: 1px
            color: rgb(147, 153, 159)
            background: #fff
        .time
          position: absolute
          top: 0
          right: 0
          line-height: 12px
          font-size: 10px
          color: rgb(147, 153, 159)
</style>



